export default {
	data() {
		return {
			couponValidList: [{
				id: 1,
				title: "服务通知",
				info:"恭喜你参加的拼团已组成成功了，请您等待卖家发货",
				time:"2019-08-08",
				img: "/static/icon-item-001.png"
			}, 
			{
				id: 2,
				title: "账户通知",
				info:"恭喜你参加的拼团已组成成功了，请您等待卖家发货",
				time:"2019-08-08",
				img: "/static/icon-item-003.png"
			}, 
			{
				id: 3,
				title: "物流通知",
				info:"恭喜你参加的拼团已组成成功了，请您等待卖家发货",
				time:"2019-08-08",
				img: "/static/icon-item-005.png"
			}, ],
			headerTop: 0,
			//控制滑动效果
			typeClass: 'valid',
			subState: '',
			theIndex: null,
			oldIndex: null,
			isStop: false
		}
	},
	methods: {
		// 立即购买
		purchase(e) {
			uni.navigateTo({
				url: "/pages/list/goode_details/goode_details"
			})
		},
		detail(e) {
			uni.navigateTo({
				url: "/pages/list/news_detail/news_detail"
			})
		},
		switchType(type) {
			if (this.typeClass == type) {
				return;
			}
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 0
			})
			this.typeClass = type;
			this.subState = this.typeClass == '' ? '' : 'show' + type;
			setTimeout(() => {
				this.oldIndex = null;
				this.theIndex = null;
				this.subState = this.typeClass == 'valid' ? '' : this.subState;
			}, 200)
		},
		//控制左滑删除效果-begin
		touchStart(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		touchMove(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];

			if (this.isStop || Math.abs(moveX) < 5) {
				return;
			}
			if (Math.abs(moveY) > Math.abs(moveX)) {
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}

			if (moveX < 0) {
				this.theIndex = index;
				this.isStop = true;
			} else if (moveX > 0) {
				if (this.theIndex != null && this.oldIndex == this.theIndex) {
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(() => {
						this.oldIndex = null;
					}, 150)
				}
			}
		},

		touchEnd(index, $event) {
			//解除禁止触发状态
			this.isStop = false;
		},

		//删除商品
		deleteCoupon(id, List) {
			let len = List.length;
			for (let i = 0; i < len; i++) {
				if (id == List[i].id) {
					List.splice(i, 1);
					break;
				}
			}
			this.oldIndex = null;
			this.theIndex = null;
		},

		discard() {
			//丢弃
		}


	}
}
